import React from 'react'
import '../index.css'
import { useState, useEffect } from 'react'
import TrackVisibility from 'react-on-screen';
import makeagame from '../images/makeagame.png'

const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(100 - Math.random() * 100);
    const toRotate = ["Rekabet", "Yaratıcılık", "Deneyim", "Ödül"];
    const period = 2000;

    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta);

        return () => { clearInterval(ticker) };
    }, [text])

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

        setText(updatedText);

        if (isDeleting) {
            setDelta(prevDelta => prevDelta / 2);
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true);
            // setIndex(prevIndex => prevIndex - 1);
            setDelta(period);
        } else if (isDeleting && updatedText === '') {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            // setIndex(1);
            setDelta(300);
        } else {

            // setIndex(prevIndex => prevIndex + 1);
        }
    }


    return (
        <section className="bg-cover bg-center home-section h-[90vh] md:h-[90vh]  " >

            <div className="h-screen w-full  flex justify-center items-center">
                <TrackVisibility >
                    {({ isVisible }) =>
                        <div className="grid md:grid-cols-2 grid-cols-1 items-center mb-20 mt-[10vh] md:mt-0  ">

                            <div className="animate__animated animate__backInDown md:m-4 p-4  pb-4 md:pb-1 md:mt-0" >
                                <h2 className='md:text-[7vh] text-[6vh] primarycolor  text-center font-bold '>YILDIZ JAM </h2>
                                <h2 className='md:text-[7vh] text-[6vh] primarycolor  text-center'>SKY LAB Kulübü</h2>
                                <h5 className='md:text-[3vh] text-[3vh] text-gray-200 secondarycolor m-3 text-center'>Yıldız Jam'de {<br />} neler var?</h5>
                                <div className="thirdcolor h-0 text-[5vh] md:text-[10vh] p-3 text-center mt-6">
                                    <span className='lexend'>{text}</span>
                                </div>
                            </div>

                            <div className='justify-center items-center flex  drop-shadow-4xl animate__animated animate__backInDown'>
                                <img className="md:mt-[3vh] md:max-h-[90vh] md:max-w-[90vh] max-h-[40vh] max-w-[40vh] " src={makeagame} alt="images" />
                            </div>
                        </div>
                    }
                </TrackVisibility>

            </div>
        </section >
    )
}

export default Banner       